/*
 This file contains links style
*/
a{
    text-decoration: none;
    cursor: pointer;
}

.a-blue{
    color: color("blue");
    &:hover{
        color: darken(color("blue"), 10)
    }
}
.a-violet{
    color: color("violet");
    &:hover{
        color: darken(color("violet"), 10)
    }
}